import React,{ useEffect, useState } from 'react';
import Path from '../Components/Path';
import { useDispatch, useSelector } from 'react-redux';
import { initializePaths } from '../reducers/pathReducer';
import { filterChangeAltitudeMin, filterChangeAltitudeMax, filterChangeLengthMin, filterChangeLengthMax, filterChangePark } from '../reducers/filterReducer';
import { Link } from 'react-router-dom';
import { FloatingLabel, Form } from 'react-bootstrap';

const parkList = ['Parco di Colfiorito','Parco del Monte Cucco','Parco di Monte Peglia e Selva di Meana (S.T.I.N.A)',
  'Parco del Monte Subasio','Parco del Lago Trasimeno','Parco Nazionale dei Monti Sibillini','Parco fluviale del Tevere','Parco fluviale del Nera'];

const PathsAPiedi = () => {
  const dispatch = useDispatch();

  const { altitude_min, altitude_max, length_min, length_max, parkName } = useSelector(state => state.filterParams);

  const paths = useSelector(({ paths, filterParams }) => {
    const result = paths
      .filter(path => path.average_drop > altitude_min)
      .filter(path => path.average_drop < altitude_max)
      .filter(path => path.path_length > length_min)
      .filter(path => path.path_length < length_max);

    if( (parkName).toLowerCase() !== 'tutti') {
      return result.filter(path => path.park_name===parkName);
    }else{
      return result;
    }
  }
  );

  const stylePathContainer = {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    paddingTop: '20px',
    gap: '40px'
  };

  const styleLink = {
    textDecoration: 'none',
    cursor: 'default',
    color: 'black'
  };

  useEffect(() => {
    dispatch(initializePaths());
  },[dispatch]);

  return (
    <div>
      <div>
        <FloatingLabel controlId="floatingSelect" className="" label="Seleziona un parco">
          <Form.Select onChange={(event) => dispatch(filterChangePark(event.target.value))} >
            <option>Tutti</option>
            {parkList.map( (p,idx) => <option key={idx} value={p}>{p}</option>)}
          </Form.Select>
        </FloatingLabel>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Dislivello minimo</Form.Label>
          <Form.Control
            type="number"
            placeholder="min"
            value={altitude_min}
            onChange={(event) => dispatch(filterChangeAltitudeMin(event.target.value))}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Dislivello massimo</Form.Label>
          <Form.Control
            type="number"
            placeholder="max"
            value={altitude_max}
            onChange={(event) => dispatch(filterChangeAltitudeMax(event.target.value))}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Lunghezza minima</Form.Label>
          <Form.Control
            type="number"
            placeholder="min"
            value={length_min}
            onChange={(event) => dispatch(filterChangeLengthMin(event.target.value))}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Lunghezza massima</Form.Label>
          <Form.Control
            type="number"
            placeholder="max"
            value={length_max}
            onChange={(event) => dispatch(filterChangeLengthMax(event.target.value))}
          />
        </Form.Group>
      </div>
      <div style={stylePathContainer}>
        {paths
          .map((path) => {
            return(
              <Link style={styleLink} to={path.id} key={ path.id }>
                <Path
                  title = { path.title }
                  park_name = {path.park_name }
                  path_numbers = {path.path_numbers }
                  starting_point = {path.starting_point }
                  description_it = { path.description_it }
                  path_length = { path.path_length }
                  average_time = { path.average_time }
                  average_drop= { path.average_drop }
                  loop = { path.loop }
                  difficult = { path.difficult }
                  id = { path.id }
                />
              </Link>
            );
          })}
      </div>
    </div>);
};

export default PathsAPiedi;
