import React,{ useState } from 'react';

const Path = ({ title, park_name, starting_point, path_length, average_time, average_drop, description_it, difficult, loop, path_numbers, id }) => {

  const [pathBrightness, setPathBrightness] = useState('brightness(100%)');

  const pathStyle = {
    flexBasis: '425px',
    boxShadow: '1px 1px 8px rgba(0, 0, 0, 0.25)',
    borderRadius: '10px',
    display: 'flex',
    width: '350px',
    flexDirection: 'column',
    padding: '30px',
    paddingBottom: '20px',
    backgroundColor: 'white',
    filter: `${pathBrightness}`
  };

  return (
    <div
      style={pathStyle}
      onMouseEnter={() => setPathBrightness('brightness(90%)')}
      onMouseLeave={() => setPathBrightness('brightness(100%)')}
    >
      <h4>{title}</h4>
      <h6>PARCO: {park_name}</h6>
      <h6>PUNTO PARTENZA: {starting_point}</h6>
      <h6>LUNGHEZZA: {path_length} Km</h6>
      <h6>TEMPO PERCORRENZA MEDIO: {average_time / 60 } m.</h6>
      <h6>DISLIVELLO: {average_drop} m.</h6>
      <h6>DIFFICOLTA:  {difficult}</h6>
      <h6>ANELLO: {loop ? 'Si' : 'No'} </h6>
      <h6>{description_it.slice(0,150).trim()}...</h6>
    </div>
  );
};

export default Path;