const gpxparsed = (gpxfile) => {
  const gpx = new GpxParser();
  gpx.parse(gpxfile);
  return gpx;
};

const SinglePath = () => {
  const [specificPath, setSpecificPath] = useState(null);
  const { pathId } = useParams();
  const navigate = useNavigate();

  const styleDiv = {
    padding: '30px',
    boxShadow: '0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1)',
    margin: '30px 5px 20px 5px',
  };

  useEffect(async () => {
    const tempPathSpec = await pathsService.getOne(pathId);
    setSpecificPath(tempPathSpec);
  },[pathId]);


  const renderPath = () => {
    return(
      <div style={styleDiv}>
        <Button variant='outline-primary' className='float-right' onClick={() => navigate('/paths_a_piedi')}>Torna</Button>
        <h2>{specificPath.title}</h2>
        <br/>
        <h6>{specificPath.park_name}</h6>
        <a href={'https://maps.google.com?q='+specificPath.starting_point}>{specificPath.starting_point}</a>
        <h6>[Meteo con posizione del luogo]</h6>
        <h5>{specificPath.path_length} Km</h5>
        <h5>{specificPath.average_time} h.</h5>
        <h5>{specificPath.average_drop} m.</h5>
        <h5>{specificPath.difficult}</h5>
        <h5>{specificPath.description_it}</h5>
        <h6>ANELLO:{specificPath.loop ? 'Si' : 'No'}</h6>
        <a href={''+specificPath.pdf}>Scarica pdf</a>
        <h6>[Numero dei sentieri - da aggiungere al database]</h6>
        {(specificPath.gpx!=='') && <MapComp mapsPoint={gpxparsed(specificPath.gpx).tracks[0].points.map(p => [p.lat, p.lon])} allgpx={gpxparsed(specificPath.gpx)} />}
      </div>);
  };

  const errorScreen = () => {
    return(
      <div style={styleDiv}>
      </div>);
  };

  return (
    <>
      {specificPath ? renderPath() : errorScreen() }
    </>
  );
};

export default SinglePath;
